<template>
<div id="rentCar">
   <h2>商家如何查看运营数据？</h2>
   <p>商家可在工作台数据看板查看收益数据、车辆数据；可在工作台-数据统计模块查看订单、收益数据</p>
  <p>今日收益=天天租用户提车后订单金额+分时租用户完成订单金额</p>
  <p>今日浏览=用户访问店铺次数</p>
  <p>今日订单金额=今日产生租车订单总金额，包括所有状态的天天租车订单、分时租车订单。</p>
  <p>今日完成订单金额=今日已完成天天租车、分时租车订单。</p>
  <p>出租中：出租中车辆</p>
  <p>待归还：车辆租期今日到期及已逾期未归还的车辆</p>
  <p>未出租：还未出租的车辆；已下架：已下架不能出租的车辆。</p>
</div>
</template>

<script>
    export default {
        name: "rentCar"
    }
</script>

<style scoped lang="scss">

</style>



